import { h } from '@stencil/core';
import { newSpecPage } from '@stencil/core/testing';
import { RwcPostCard } from '../post-card';

describe('rwc-post-card', () => {
  it('should render the post card', async () => {
    const page = await newSpecPage({
      components: [RwcPostCard],
      template: () => (
        <rwc-post-card
          renderShadow={ true }
          renderBorder={ true }
          animateBorder={ true }
          pointer={ true }
        >
          Card Text
        </rwc-post-card>
      ),
    });

    expect(page.root).toEqualHtml(`
      <rwc-post-card
        render-shadow=""
        render-border=""
        animate-border=""
        avatar=""
      >
        <mock:shadow-root>
          <rwc-card
            rendershadow=""
            renderborder=""
            animateborder=""
            pointer=""
            class="card"
          >
            <div class="container">
              <div class="user">
                <div class="avatar">
                  <slot name="avatar">
                    <div class="svg-icon"></div>
                  </slot>
                </div>
                <div class="name">
                  <slot name="name"></slot>
                </div>
              </div>
              <div class="text">
                <div class="heading content-spacing">
                  <slot name="heading"></slot>
                </div>
                <div class="description content-spacing">
                  <slot name="description"></slot>
                </div>
                <div class="footer content-spacing">
                  <slot name="footer"></slot>
                </div>
              </div>
            </div>
          </rwc-card>
        </mock:shadow-root>
        Card Text
      </rwc-post-card>
    `);
  });
  
  it('should render the avatar by url', async () => {
    const page = await newSpecPage({
      components: [RwcPostCard],
      template: () => (
        <rwc-post-card
          renderShadow={ true }
          renderBorder={ true }
          animateBorder={ true }
          pointer={ true }
          avatar="https://test-icon.png"
        >
          Card Text
        </rwc-post-card>
      ),
    });

    expect(page.root).toEqualHtml(`
      <rwc-post-card
        render-shadow=""
        render-border=""
        animate-border=""
        avatar="https://test-icon.png"
      >
        <mock:shadow-root>
          <rwc-card
            rendershadow=""
            renderborder=""
            animateborder=""
            pointer=""
            class="card"
          >
            <div class="container">
              <div class="user">
                <div class="avatar">
                  <slot name="avatar">
                    <img src="https://test-icon.png" alt="avatar" class="icon"></img>
                  </slot>
                </div>
                <div class="name">
                  <slot name="name"></slot>
                </div>
              </div>
              <div class="text">
                <div class="heading content-spacing">
                  <slot name="heading"></slot>
                </div>
                <div class="description content-spacing">
                  <slot name="description"></slot>
                </div>
                <div class="footer content-spacing">
                  <slot name="footer"></slot>
                </div>
              </div>
            </div>
          </rwc-card>
        </mock:shadow-root>
        Card Text
      </rwc-post-card>
    `);
  });
})
